header {
  height: 100vh;
}

.navigation {
  position: relative;
  background-color: #fff;
}

.nav-inner {
  height: 15vh;
}

.row {
  height: 85vh;
}

.search-icon {
  font-size: 1.8rem;
  cursor: pointer;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  height: 100%;
  transition: all 0.3s;
}
.search-icon:active {
  transform: scale(0.9);
}

.search-bar {
  position: absolute;
  display: none;
  top: 0;
  right: 0;
  width: 35%;
  background-color: #fff;
  z-index: 100;
  height: 100vh;
}

.cross-icon {
  position: absolute;
  top: 0%;
  right: 3%;
  font-size: 3.5rem;
}

.search-bar-input {
  padding: 1.5rem;
  margin-top: 7rem;
  width: 90%;
  font-size: 1.6rem;
  border: transparent;
}
.search-bar-input:focus {
  outline: none;
}

.search-bar-outter {
  margin: 0 3rem;
  padding: 0 1rem;
  border-bottom: 0.2rem solid black;
}

.hero-section {
  background-color: #eaeaea;
}
.hero-section__right {
  height: 100%;
  display: flex;
  justify-content: center;
}
.hero-section__img {
  height: 100%;
}
.hero-section__left {
  position: relative;
}
.hero-section__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}